#start-menu {
    width: 30%;
    height: 75%;
    position: absolute;
    bottom: 30px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.name-section {
    width: 100%;
    height: 12%;
    border-radius: 7px 7px 0 0 ;
    padding-left: 4px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    background-image: linear-gradient(180.0deg, rgb(34, 90, 212) 36.7%, rgba(255, 255, 255, 1) 256.4%);    
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
}

.name-section img {
    width: 60px;
    height: 60px;
    margin:10px;
    border: 2px solid white;
    border-radius: 4px;
}

.name-section h1{
    color: white;
    text-transform: uppercase;
}

.content-section{
    width: 100%;
    height: 80%;
    background-color: white;
    border-left: 2px solid #235ddb;
    border-right: 2px solid #235ddb;
    display: flex;
    flex-direction: row;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

.left-side{
    flex-direction: column;
    height: 100%;
    width: 50%;
    background-color: white;
}

.item {
    display: flex;
    align-items: center;
    width: 80%;
    height: 10%;
    margin: 10px 0px 0px 10px;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
}

.item img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.content-section .right {
    height: 100%;
    width: 50%;
    background-color: #0051ff28;
    border-left: 1px solid #235ddb5b;
}

.power-section{
    width: 100%;
    height: 8%;
    background-color: #235ddb;
    padding-left: 4px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    display: flex;
    flex-direction: row;
    justify-content: end;
}

.power-button{
    display: flex;
    flex-direction: row;
    margin-right: 10px;
    align-items: center;
    color: white;
    cursor: pointer;
}

.power-button img{
    width: 20px;
    height: 20px;
    margin-right: 5px;
}